'use client'

import MovieCard from "@/app/components/MovieCard";
import movie from "@/app/movie/page";
import axios from "axios";
import { useEffect, useState } from "react";

interface movieObj{
    _id: number,
    movie_id: number,
    title: string,
    release: number,
    director: string,
    description: string,
    rating: number,
    categories: Array<number>,
     __v: number
}

const API  = ()=>{
    const [movies, setMovies] = useState<Array<movieObj>>([]);
    useEffect(()=>{
        axios.get('http://localhost:8000/movie').then((res)=>{
            // console.log(`Movies : ${JSON.stringify(res)}`);
            setMovies(res.data)
            
        })
        .catch((err)=>{
            console.log(`Error : ${err}`);
            
        })
    });


    return (
        <>
        <h1>APIs </h1>
        <div className="container m-auto grid items-center text-center grid-cols-4 gap-3">
        {
                movies.map( ( data: movieObj , index)=>(
                    <>
                    
                    <h1>{data.movie_id}</h1>
                    <h1>{data.title}</h1>
                    <h2 className="text-justify">{data.description}</h2>
                    <h3>{data.release}</h3>
                    </>
                ))
            }
        </div>
        </>
    )
};

export default API